<template>
  <div class="share">
    <p class="diggit" @click="praiseBlog(blogUid)">
      <a href="javascript:void(0);"></a>
      <span v-if="praiseCount!= 0">
        (<b id="diggnum">{{praiseCount}}</b>)
      </span>
    </p>
    <p class="dasbox"  @click="collectToggle()">
      <a href="javascript:void(0)"  class="collect" title=""></a>
    </p>
    <p class="reportbtn"  @click="reportToggle()">
      <a href="javascript:void(0)"  class="report" title=""></a>
    </p>
    <div class="hide_box" v-if="showCollect"></div>
    <div class="shang_box" v-if="showCollect">
      <a class="shang_close" href="javascript:void(0)" @click="collectToggle()" title=""></a>
      <el-form label-position="top" >
        <el-form-item label="" prop="userName">
          <el-input v-model="collectName" placeholder=""></el-input>
        </el-form-item>
        <el-row class="btn">
          <el-button class="agreeBtn" type="primary" @click="collectBlog(blogUid,collectName)" ></el-button>
          <el-button class="cancelBtn" type="info" @click="collectToggle()" ></el-button>
        </el-row>
      </el-form>
    </div>
    <div class="shang_box" v-if="showReport">
      <a class="shang_close" href="javascript:void(0)" @click="reportToggle()" title=""></a>
      <el-form label-position="top" >
        <el-form-item label="" prop="reportReason">
          <el-input v-model="reportReason" placeholder=""></el-input>
        </el-form-item>
        <el-row class="btn">
          <el-button class="agreeBtn" type="primary" @click="reportBlog(blogUid,reportReason)" ></el-button>
          <el-button class="cancelBtn" type="info" @click="reportToggle()" ></el-button>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
import { getWebConfig } from '../../api/index'
import {
  praiseBlogByUid, getBlogPraiseCountByUid, addCollectBlog, reportBlog
} from '../../api/blogContent'
import {mapMutations} from 'vuex'
export default {
  name: 'LikeAndCollect',
  props: {
    praiseCount: {
      type: Number,
      default: 0
    },
    blogUid: ''
  },
  data () {
    return {
      showCollect: '',
      collectName: '',
      showReport: '',
      reportReason: ''
    }
  },
  methods: {
    // vuex
    ...mapMutations(['setLoginMessage']),
    collectToggle: function () {
      // this.showPay = !this.showPay
      if (!this.$store.state.user.isLogin) {
        this.$notify.error({
          title: '',
          message: '~',
          offset: 100
        })
        // 
        this.setLoginMessage(Math.random())
        return
      }
      this.showCollect = !this.showCollect
    },
    reportToggle: function () {
      // this.showPay = !this.showPay
      if (!this.$store.state.user.isLogin) {
        this.$notify.error({
          title: '',
          message: '~',
          offset: 100
        })
        // 
        this.setLoginMessage(Math.random())
        return
      }
      this.showReport = !this.showReport
    },
    // 
    praiseBlog: function (uid) {
      // 
      let isLogin = this.$store.state.user.isLogin
      if (!isLogin) {
        this.$notify.error({
          title: '',
          message: '~',
          offset: 100
        })
        // 
        this.setLoginMessage(Math.random())
        return
      }

      var params = new URLSearchParams()
      params.append('uid', uid)
      praiseBlogByUid(params).then(response => {
        if (response.data.code === this.$ECode.SUCCESS) {
          this.$notify({
            title: '',
            message: '',
            type: 'success',
            offset: 100
          })
          this.praiseCount = response.data.number
          this.$emit('praise', this.praiseCount)
        } else {
          this.$notify({
            title: '',
            type: 'info',
            message: response.data.message,
            offset: 100
          })
        }
      })
    },
    // 
    getPraiseCount: function (uid) {
      var params = new URLSearchParams()
      params.append('uid', uid)
      getBlogPraiseCountByUid(params).then(response => {
        if (response.data.code === this.$ECode.SUCCESS) {
          this.praiseCount = response.data.number
        }
      })
    },
    collectBlog: function (uid, collectName) {
      let that = this
      var params = new URLSearchParams()
      params.append('uid', uid)
      params.append('collectName', collectName)
      console.log('111111 ' + collectName)
      addCollectBlog(params).then(response => {
        if (response.data.code === this.$ECode.SUCCESS) {
          that.collectToggle()
        } else {
          this.$message({
            type: 'error',
            message: response.data.message
          })
        }
      }).catch(function () {
        that.collectToggle()
      })
    },
    reportBlog: function (uid, reportReason) {
      let that = this
      var params = new URLSearchParams()
      params.append('uid', uid)
      params.append('reason', reportReason)
      console.log(reportReason)
      console.log(params)
      reportBlog(params).then(response => {
        if (response.data.code === this.$ECode.SUCCESS) {
          that.reportToggle()
        }
      }).catch(function () {
        that.reportToggle()
      })
    }
  }
}
</script>

<style>
.diggit {
  cursor: pointer;
}
.dasbox {
  cursor: pointer;
}
.registerBtn{
  cursor: pointer;
}
</style>



